<template>
  <div class="date2">
    <p>
      殷利宝宝专用日期计算器
    </p>
    <p @click="openPicker">
      选择开始日期： {{startDate}}
    </p>
    <p @click="openPicker2">
      选择结束日期： {{endDate}}
    </p>
    <p>
      间隔天数： {{days}}
    </p>
    <mt-datetime-picker ref="picker" type="date" @confirm="handleConfirm" v-model="pickerValue">

    </mt-datetime-picker>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pickerValue: '',
      startDate: '',
      endDate: '',
      start: false,
      days: '',
    };
  },
  methods: {
    openPicker() {
      this.start = true;
      this.$refs.picker.open();
    },
    openPicker2() {
      this.start = false;
      this.$refs.picker.open();
    },
    formDate(val) {
      const date = new Date(val);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const date2 = date.getDate();
      return `${year}-${month < 10 ? `0${month}` : month}-${date2 < 10 ? `0${date2}` : date2}`;
    },
    handleConfirm(val) {
      if (this.start) {
        this.startDate = this.formDate(val);
      } else {
        this.endDate = this.formDate(val);
      }
      this.compulateDate();
    },
    compulateDate() {
      const { startDate, endDate } = this;
      const time1 = new Date(startDate).getTime();
      const time2 = new Date(endDate).getTime();
      if (time1 && time2) {
        this.getDaysByDateString(startDate, endDate);
      }
    },
    getDaysByDateString(dateString1, dateString2) {
      const startDate = Date.parse(dateString1.replace('/-/g', '/'));
      const endDate = Date.parse(dateString2.replace('/-/g', '/'));
      const diffDate = (endDate - startDate) + (1 * 24 * 60 * 60 * 1000);
      const days = diffDate / (1 * 24 * 60 * 60 * 1000);
      this.days = days;
      // alert(diffDate/(1*24*60*60*1000));
      return days;
    },
  },
};
</script>
